import React from 'react'
import CountUp from 'react-countup'

const AnimatedCounter = ({ amount }:{ amount: number }) => {
  return (
    <div>
        <CountUp 
        duration={2} 
        separator=" " 
        decimals={2} 
        decimal="," 
        suffix ="$" 
        end={amount}  />
    </div>
  )
}

export default AnimatedCounter